import { Block, Controller } from '@ali/4ever-cangjie';

/**
 * 根据 startBlock 获取光标所在的 block（兼容排版模式）
 * line 是排版中分割的行，需要再向上一层
 * @param controller
 */
export default function getStartBlock(controller: Controller) {
  const { startBlock, document } = controller.value;
  if (!startBlock) return startBlock;

  let node: Block | null = startBlock;

  if (node.type === 'line') {
    node = document.getClosestBlock(node.key);
  }

  return node;
}
